import React, { useState, useRef, useEffect } from 'react';
import { GoogleMap, LoadScript, DrawingManager, useJsApiLoader } from '@react-google-maps/api';

const MapComponent = () => {
    const [address, setAddress] = useState('');
    const [center, setCenter] = useState(null);
    const mapRef = useRef();

    const { isLoaded } = useJsApiLoader({
        id: 'google-map-script',
        googleMapsApiKey: "YOUR_GOOGLE_MAPS_API_KEY",
    });

    const onLoad = (map) => {
        mapRef.current = map;
    };

    const onUnmount = () => {
        mapRef.current = null;
    };

    const fetchGeocode = async () => {
        const response = await fetch(`https://maps.googleapis.com/maps/api/geocode/json?address=${address}&key=YOUR_GOOGLE_MAPS_API_KEY`);
        const data = await response.json();
        if (data.results && data.results.length > 0) {
            const location = data.results[0].geometry.location;
            setCenter(location);
        }
    };

    const handleAddressChange = (e) => {
        setAddress(e.target.value);
    };

    const handleSearchClick = () => {
        fetchGeocode();
    };

    const handlePolygonComplete = (polygon) => {
        const path = polygon.getPath().getArray();
        const area = window.google.maps.geometry.spherical.computeArea(polygon.getPath());
        console.log('Polygon vertices:', path);
        console.log('Area of polygon:', area + ' square meters');
    };

    return (
        <div>
            <input type="text" value={address} onChange={handleAddressChange} placeholder="Adresse eingeben" />
            <button onClick={handleSearchClick}>Suchen</button>

            {isLoaded && center && (
                <GoogleMap
                    id="drawing-map"
                    mapContainerStyle={{ width: '100%', height: '400px' }}
                    center={center}
                    zoom={17}
                    onLoad={onLoad}
                    onUnmount={onUnmount}
                    mapTypeId="satellite"
                >
                    <DrawingManager
                        onPolygonComplete={handlePolygonComplete}
                        options={{
                            drawingControl: true,
                            drawingControlOptions: {
                                position: window.google.maps.ControlPosition.TOP_CENTER,
                                drawingModes: [window.google.maps.drawing.OverlayType.POLYGON],
                            },
                            polygonOptions: {
                                fillColor: '#ffff00',
                                fillOpacity: 1,
                                strokeWeight: 5,
                                clickable: false,
                                editable: true,
                                zIndex: 1,
                            },
                        }}
                    />
                </GoogleMap>
            )}
        </div>
    );
};

export default MapComponent;
